<script>
import { mapState } from 'vuex'
export default {
  name: 'busquedas',
  computed: {
    ...mapState(['busquedas']),
    hhh () {
      const id = parseInt(this.$route.params.id)
      return this.busquedas.filter(function (c) {
        return c.id === id
      })[0]
    }
  }
}
</script>

